onent>\n                                </div>\n                                <div v-else>\n                                    <input\n                                        type=\"file\"\n                                        @change=\"onChange\"\n                                        multiple\n                                    />\n                                </div>\n                                <hr />\n                                <div class=\"row\">\n                                    <div class=\"col-12 table-responsive\">\n                                        <p class=\"lead\">\n                                              \n                                        </p>\n\n                                        <table\n                                            class=\"table table-striped\"\n                                            style=\"\n                                                background: #edebe4;\n                                                color: #fff;\n                                            \"\n                                        >\n                                            <thead>\n                                                <tr>\n                                                    <th rowspan=\"2\"></th>\n                                                    <th\n                                                        v-if=\"this.model.id\"\n                                                        rowspan=\"2\"\n                                                    >\n                                                        \n                                                    </th>\n                                                    <th rowspan=\"2\"></th>\n                                                </tr>\n                                            </thead>\n                                            <draggable\n                                                v-model=\"expenses\"\n             