tedBranchs), // Ensure it's JSON string\n            };\n\n            this.$http\n                .post(action, formData, {\n                    \"Content-Type\": \"multipart/form-data\",\n                    \"X-CSRF-TOKEN\": token,\n                })\n                .then(async (response) => {\n                    Util.useSwall(response).then((result) => {\n                        if (result.value) {\n                            window.location.replace(\n                                this.setting.url.request.index.replace(\n                                    \"api/\",\n                                    \"\"\n                                )\n                            );\n                        } else if (\n                            this.model.id == null &&\n                            response.data.success == true\n                        ) {\n                            window.location.replace(\n                                window.location.href.replace(\n                                    \"new\",\n                                    response.data.result.id\n                                )\n                            );\n                        }\n\n                        if (!response.data.success) {\n                            if (response.data.errs)\n                                response.data.errs.map((item) =>\n                                    this.$toast.error(item, {\n                                        position: \"top-right\",\n                                        duration: 7000,\n                                    })\n                                );\n                        }\n                    });\n                })\n                .catch((e) => {\n                    this.$toast.error(e.response.statusText, {\n                        position: \"top-right\",\n                        duration: 7000,\n                    });\n                });\n\n            return false;\n        },\n\n        try_focus() {\n